import * as React from "react"
import Layout from "../components/layout"
import SeoBusiness from "../components/seoBusiness"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"

const GalleryPage = ({ data }) => {
  const allImages = data.allFile.edges
  const getImageFromGatsby = image => getImage(image)
  return (
    <Layout>
      <SeoBusiness title="Gallery" />
      <section className="page-section">
        <div className="container">
          <div className="row">
            {allImages &&
              allImages.map(image => (
                <div className="product-item col-md-6 mb-4" key={image.node.id}>
                  <div className="left-0 text-center rounded">
                    <GatsbyImage
                      className="product-item-img mx-auto d-flex rounded img-fluid mb-3 mb-lg-0 custom-box-shadow"
                      image={getImageFromGatsby(image.node)}
                      quality={95}
                      width={500}
                      placeholder="blurred"
                      formats={["auto", "webp", "avif"]}
                      alt="sun"
                    />
                  </div>
                </div>
              ))}
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default GalleryPage

export const pageQuery = graphql`
  query GalleryPhotos {
    allFile(
      filter: {
        extension: { regex: "/(jpg)|(jpeg)|(png)/" }
        sourceInstanceName: { eq: "gallery" }
      }
    ) {
      edges {
        node {
          id
          name
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`
